/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Delius Unicase", serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 200rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Delius Unicase", serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: rgb(189.6789699571, 114.3154506438, 7.6210300429);
  cursor: pointer;
}

.boton-amarillo {
  background-color: #E08709;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: rgb(189.6789699571, 114.3154506438, 7.6210300429);
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: rgb(90.2084745763, 141.3, 0);
  cursor: pointer;
}

.boton-violeta {
  background-color: #9C27B0;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: flex;
  border: none;
  flex-direction: row;
  justify-content: center;
  margin: 0;
}
.boton-violeta:hover {
  background-color: rgb(130.096744186, 32.5241860465, 146.7758139535);
  cursor: pointer;
}
.boton-violeta img {
  width: 5rem;
  align-items: center;
}

.no-webp .inicio {
  background-image: url(/build/img/nosotros.jpg);
}

.webp .inicio {
  background-image: url(/build/img/nosotros.webp);
}

.overlay {
  position: relative;
  width: 100%;
  background-color: rgba(33, 28, 28, 0.5);
  background-color: rgba(33, 28, 28, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

header {
  background-color: #333333;
}
header h1 {
  text-align: left;
  color: #efb810;
  font-weight: 900;
  line-height: 1;
  max-width: 30;
  margin-left: 4rem;
  padding-bottom: 2rem;
}
header.inicio {
  background-position: center center;
  background-size: cover;
}
header .contenido-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
header .contenido-header.fixed {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: #333333;
  height: 10rem;
}
header .navegacion a:hover {
  color: #efb810;
  border-radius: 1rem;
}
header .navegacion a:hover img {
  background-color: #efb810;
}

.derecha img {
  width: 4rem;
}
.derecha img:hover {
  background-color: #efb810;
  cursor: pointer;
}

.dark-mode-boton {
  width: 3rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .dark-mode-boton {
    width: 5rem;
    margin-top: 0;
    margin-bottom: 0;
  }
}

.barra {
  display: contents;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion, .navegacion2 {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.5s linear;
  height: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion, .navegacion2 {
    display: flex;
    flex-direction: row;
    gap: 3rem;
    margin-top: 2rem;
    visibility: visible;
  }
}
.navegacion a, .navegacion2 a {
  color: #68E5D9;
  text-decoration: none;
  font-size: 2.2rem;
}
.navegacion a img, .navegacion2 a img {
  width: 5rem;
}
@media (min-width: 768px) {
  .navegacion, .navegacion2 {
    margin-right: 2rem;
  }
  .navegacion:last-of-type, .navegacion2:last-of-type {
    margin-right: 0;
  }
}

.mobile-menu,
.mobile-menu2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .mobile-menu,
  .mobile-menu2 {
    display: none;
  }
}
.mobile-menu img,
.mobile-menu2 img {
  width: 5rem;
}
.mobile-menu img:hover,
.mobile-menu2 img:hover {
  cursor: pointer;
  background-color: rgb(239.32, 185.42, 20.78);
}

.mostrar, .mostrar2 {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.carrito img {
  width: 4rem;
  margin-top: 3rem;
}
.carrito img:hover {
  background-color: #efb810;
  cursor: pointer;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer a:hover {
  background-color: #e1e1e1;
  color: #000000;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyringht {
  text-align: center;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos {
  text-align: center;
}
.iconos img {
  height: 10rem;
}
.iconos h3 {
  text-transform: uppercase;
}

.imagen-contacto {
  background-image: url(/build/img/grafico1.jpg);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto .overlay h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #68E5D9;
}
.imagen-contacto .overlay p {
  font-size: 1.8rem;
  font-weight: 400;
  color: #FFFFFF;
}
.imagen-contacto .overlay .boton-amarillo {
  margin-bottom: 3rem;
}

.contenedor-anuncio {
  margin: 3rem;
}
@media (min-width: 768px) {
  .contenedor-anuncio {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: rgb(255, 206.9310344828, 193.5);
  border: 1px solid rgb(118.9953488372, 29.7488372093, 134.2511627907);
  padding: 2rem;
  margin: 1rem;
  align-content: center;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio h4, .anuncio p {
  margin: 0;
}

.precio {
  color: #000000;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  flex-direction: row;
  justify-content: center;
  list-style: none;
  padding: 1;
  max-width: 5rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
  justify-content: left;
  margin: 0;
}
.iconos-caracteristicas li img {
  margin-left: 3rem;
}

ul {
  list-style: none;
}

.submenu {
  position: relative;
}

.submenu #carrito {
  display: none;
}

.submenu:hover #carrito {
  display: block;
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 1;
  background-color: white;
  padding: 20px;
  min-height: 400px;
  min-width: 300px;
}

.u-full-width {
  width: 100%;
  box-sizing: border-box;
}

.columns {
  width: 100%;
  float: left;
  box-sizing: border-box;
}

/* For devices larger than 400px */
@media tablet {
  .container {
    width: 85%;
    padding: 0;
  }
}
.u-pull-right {
  float: right;
}

.borrar-seleccion {
  background-color: #f90505;
  border-radius: 50%;
  padding: 5px 10px;
  text-decoration: none;
  color: white;
  font-weight: bold;
}
.borrar-seleccion:hover {
  background-color: rgb(214.0027559055, 4.2972440945, 4.2972440945);
  cursor: pointer;
}

.dark-mode {
  background-color: rgb(25.5, 25.5, 25.5);
}
.dark-mode .inicio .overlay {
  background-color: rgba(33, 28, 28, 0.8);
  background-color: rgba(33, 28, 28, 0.8);
}
.dark-mode P {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .icono {
  filter: invert(100%);
}
.dark-mode .anuncio {
  background-color: #333333;
  border: solid 1px #333333;
}
.dark-mode .formulario legend {
  color: #e1e1e1;
}
.dark-mode .formulario label {
  color: #FFFFFF;
}
.dark-mode .formulario .icono-formulario {
  filter: invert(100%);
}